html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: Oswald, sans-serif;
  font-style: normal;
  font-weight: 400;
  color: @title-color;

}

.visually-hidden {
  display: none;
}

a {
  text-decoration: none;
}

ul, ol {
  list-style-type:  none;
  margin: 0;
  padding: 0;
}


