html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  margin: 0;
  padding: 0;
  font-family: Oswald, sans-serif;
  font-style: normal;
  font-weight: 400;
  color: #000000;
}
.visually-hidden {
  display: none;
}
a {
  text-decoration: none;
}
ul,
ol {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
@font-face {
  font-family: Oswald;
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/oswaldregular.woff2) format("woff2"), url(../fonts/oswaldregular.woff) format("woff");
  font-display: swap;
}
@font-face {
  font-family: Oswald;
  font-style: normal;
  font-weight: 500;
  src: url(../fonts/oswaldmedium.woff2) format("woff2"), url(../fonts/oswaldmedium.woff) format("woff");
  font-display: swap;
}
